.container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    border: 1px solid black;
    height: 100%;
    width: 70%;
    margin: 0 auto;
}

@media screen and (max-width: 1028px) {
    .container {
        justify-content: center;
    }
}
