.skill {
    width: 270px;
    min-height: 200px;
    left: 365px;
    top: 900px;
    background: #2A2A2A;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.47);
    border-radius: 29px;
    padding: 25px 0 0 0;
    margin: 10px;

}

.text {
    transform: rotate(-45deg);
}
.icon {
    color: #4e93e6;
    height: 80px;
    width: 80px;
    margin: 0px auto 30px;
    background: rgba(255,255,255,0.1);
    display: block;
    line-height: 80px;
    font-size: 30px;
    /*box-shadow: -6px 10px 40px rgb(0 0 0 / 6%);*/
    text-align: center;
    transform: rotate(45deg);
    transform-origin: center;
}

.description {
    padding: 5px;
}